import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { Link, useNavigate, useParams } from 'react-router-dom';
import { CreateUser, UpdateUser, getUserById } from '../../../services/user.services';
import { ToastContainer, toast } from 'react-toastify';
import { UsersType } from '../../../types';
import { checkEmptyError, checkSelectEmptyError, handleFocusInput } from '../../../utils/global';
import {
    checkBirthDayError,
    checkEmailError,
    checkNameError,
    checkPasswordError,
    checkPhoneError,
} from '../../../utils/validation_user';
import { useRecoilValue } from 'recoil';
import { userValue } from '../../../store/user.atom';

type DataParams = {
    id: string;
};

export const AddUser = () => {
    let inputEmail: any;
    let inputPhone: any;
    let inputPassword: any;
    let inputBirthday: any;
    let inputName: any;
    let listInputText: any;
    let listInputDate: any;
    let listInputPassword: any;
    let listSelect: any;
    const navigate = useNavigate();
    const { id } = useParams<DataParams>();
    const [provinces, setProvinces] = useState([{ province_id: 0, province_name: '' }]);

    const [districts, setDistricts] = useState([{ district_id: 0, district_name: '' }]);
    const [wards, setWards] = useState([{ ward_id: 0, ward_name: '' }]);
    const userInfo = useRecoilValue(userValue);
    const [user, setUser] = useState<UsersType>({
        id: 0,
        password: '',
        role: 1,
        active: true,
        us_name: '',
        email: '',
        phone_number: '',
        birthday: '',
        created_at: '',
        updated_at: '',
        province: '',
        district: '',
        ward: '',
        token: '',
    });
    const [showPassword, setShowPassword] = useState(false);

    useEffect(() => {
        const queryElement = () => {
            listInputText = document.querySelectorAll('.form-add input[type="text"]');
            listInputDate = document.querySelectorAll('.form-add input[type="date"]');
            listInputPassword = document.querySelectorAll('.form-add input[type="password"]');
            listSelect = document.querySelectorAll('.form-add select');
            inputEmail = document.querySelector('#email');
            inputPhone = document.querySelector('#phonenumber');
            inputPassword = document.querySelector('#password');
            inputName = document.querySelector('#pro_name');
            inputBirthday = document.querySelector('#birthday');
        };
        queryElement();
    });
    useEffect(() => {
        const getProvinces = async () => {
            try {
                const res = await axios.get('https://vapi.vnappmob.com/api/province');
                setProvinces(res.data.results);
            } catch (err) {
                console.log(err);
            }
        };
        const setEventFocusInput = () => {
            handleFocusInput(listInputDate);
            handleFocusInput(listInputText);
            handleFocusInput(listInputPassword);
            handleFocusInput(listSelect);
        };
        getProvinces();
        setEventFocusInput();
    }, []);
    const getListDistrict = async (provinceId: any) => {
        try {
            const res = await axios.get(`https://vapi.vnappmob.com//api/province/district/${provinceId}`);
            setDistricts(res.data.results);
        } catch (err) {
            console.log(err);
        }
    };
    const getWards = async (districtId: any) => {
        try {
            const res = await axios.get(`https://vapi.vnappmob.com//api/province/ward/${districtId}`);
            setWards(res.data.results);
        } catch (err) {
            console.log(err);
        }
    };
    useEffect(() => {
        async function getUser() {
            if (id !== undefined) {
                try {
                    const res = await getUserById(id);
                    setUser(res);
                } catch (e) {
                    console.error(e);
                }
            }
        }
        getUser();
    }, [id]);
    useEffect(() => {
        if (user.id !== 0) {
            let province: any = provinces.find((item) => {
                return item.province_name.includes(user.province);
            });
            if (province !== undefined) {
                getListDistrict(province.province_id);
            }
        }
    }, [provinces.length]);
    useEffect(() => {
        if (districts.length > 0) {
            let district: any = districts.find((item) => {
                return item.district_name.includes(user.district);
            });
            if (district !== undefined) {
                getWards(district.district_id);
            }
        }
    }, [districts.length]);
    const handleCreateProduct = () => {
        const isInputTextEmpty = checkEmptyError(listInputText);
        const isInputDateEmpty = checkEmptyError(listInputDate);
        const isInputPasswordEmpty = checkEmptyError(listInputPassword);
        const isSelectEmpty = checkSelectEmptyError(listSelect);
        if (!isInputTextEmpty && !isInputDateEmpty && !isInputPasswordEmpty && !isSelectEmpty) {
            const isEmailError = checkEmailError(inputEmail);
            const isPhoneError = checkPhoneError(inputPhone);
            const isBirthDayError = checkBirthDayError(inputBirthday);
            const isPasswordError = checkPasswordError(inputPassword);
            const isNameError = checkNameError(inputName);
            if (!isEmailError && !isPhoneError && !isPasswordError && !isBirthDayError && !isNameError) {
                const province = provinces.find((p) => p.province_id == Number(user.province));
                const district = districts.find((d) => d.district_id == Number(user.district));
                const ward = wards.find((d) => d.ward_id == Number(user.ward));
                let data = {};
                if (userInfo.user.role === 1) {
                    data = {
                        password: user.password,
                        us_name: user.us_name,
                        email: user.email,
                        phone_number: user.phone_number,
                        birthday: user.birthday,
                        province: province?.province_name,
                        district: district?.district_name,
                        ward: ward?.ward_name,
                        token: '',
                        role_id: 2,
                    };
                } else {
                    data = {
                        password: user.password,
                        us_name: user.us_name,
                        email: user.email,
                        phone_number: user.phone_number,
                        birthday: user.birthday,
                        province: province?.province_name,
                        district: district?.district_name,
                        ward: ward?.ward_name,
                        token: '',
                        role_id: 1,
                    };
                }

                Create(data);
            }
        }
    };
    const handleUpdate = () => {
        const isInputTextEmpty = checkEmptyError(listInputText);
        const isInputDateEmpty = checkEmptyError(listInputDate);
        const isInputPasswordEmpty = checkEmptyError(listInputPassword);
        if (!isInputTextEmpty && !isInputDateEmpty && !isInputPasswordEmpty) {
            const isEmailError = checkEmailError(inputEmail);
            const isPhoneError = checkPhoneError(inputPhone);
            const isBirthDayError = checkBirthDayError(inputBirthday);
            const isPasswordError = checkPasswordError(inputPassword);
            const isNameError = checkNameError(inputName);
            if (!isEmailError && !isPhoneError && !isPasswordError && !isBirthDayError && !isNameError) {
                const province = provinces.find((p) => p.province_id == Number(user.province));
                const district = districts.find((d) => d.district_id == Number(user.district));
                const ward = wards.find((d) => d.ward_id == Number(user.ward));
                const data = {
                    id: user.id,
                    password: user.password,
                    us_name: user.us_name,
                    email: user.email,
                    phone_number: user.phone_number,
                    birthday: user.birthday,
                    created_at: user.created_at,
                    province: province?.province_name === undefined ? user.province : province?.province_name,
                    district: district?.district_name === undefined ? user.district : district?.district_name,
                    ward: ward?.ward_name == undefined ? user.ward : ward?.ward_name,
                    token: '',
                };
                Update(data);
            }
        }
    };

    const Create = async (data: any) => {
        try {
            const res = await CreateUser(data);
            if (res.status === 200) {
                navigate('/admin/user');
                toast.success('Thm thnh cng', {
                    position: 'top-right',
                    autoClose: 3000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: 'light',
                });
            } else {
                console.log(res);
                toast.error('C li', {
                    position: 'top-right',
                    autoClose: 3000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: 'light',
                });
            }
        } catch (err) {
            console.log(err);
            toast.error('C li', {
                position: 'top-right',
                autoClose: 3000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                theme: 'light',
            });
        }
    };

    const Update = async (data: any) => {
        try {
            const res = await UpdateUser(data);
            if (res.status === 200) {
                navigate('/admin/user');
                toast.success('Sa thnh cng', {
                    position: 'top-right',
                    autoClose: 3000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: 'light',
                });
            } else {
                console.log(res);

                toast.error('C li', {
                    position: 'top-right',
                    autoClose: 3000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: 'light',
                });
            }
        } catch (err) {
            console.log(err);
            toast.error('C li', {
                position: 'top-right',
                autoClose: 3000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                theme: 'light',
            });
        }
    };
    return (
        <div id="form">
            <div className="card form-add">
                <div className="card-header">
                    <h1>{id === undefined ? 'Thm ngi dng' : `Sa thng tin ngi dng c m ${id}`} </h1>
                </div>
                <div className="card-body">
                    <form>
                        <div className="row">
                            <h3 className="title">Thng tin khch hng</h3>
                            <div className="col-lg-6">
                                {id !== undefined && (
                                    <>
                                        <input id="product_id" className="form-control" value={user.id} hidden></input>
                                        <div className="error_message" style={{ display: 'none' }}></div>
                                    </>
                                )}
                                <div className="form-group">
                                    <label htmlFor="pro_name">Tn ngi dng:</label>
                                    <input
                                        type="text"
                                        name="pro_name"
                                        id="pro_name"
                                        className="form-control"
                                        onChange={(e) => setUser({ ...user, us_name: e.target.value })}
                                        value={user.us_name}
                                    ></input>
                                    <div className="error_message" style={{ display: 'none' }}></div>
                                </div>

                                <div className="form-group">
                                    <label htmlFor="province">Tnh/thnh ph:</label>
                                    <select
                                        name="province"
                                        id="province"
                                        className="form-control"
                                        onChange={(e) => {
                                            setUser({ ...user, province: e.target.value });
                                            getListDistrict(e.target.value);
                                        }}
                                    >
                                        <option value="0">Chn tnh/thnh ph</option>
                                        {provinces.map((item: any) => {
                                            if (id && item.province_name.includes(user.province)) {
                                                return (
                                                    <option value={item.province_id} key={item.province_id} selected>
                                                        {item.province_name}
                                                    </option>
                                                );
                                            } else
                                                return (
                                                    <option value={item.province_id} key={item.province_id}>
                                                        {item.province_name}
                                                    </option>
                                                );
                                        })}
                                    </select>
                                    <div className="error_message" style={{ display: 'none' }}></div>
                                </div>
                                <div className="form-group">
                                    <label htmlFor="district">Qun/Huyn:</label>
                                    <select
                                        name="district"
                                        id="district"
                                        className="form-control"
                                        onChange={(e) => {
                                            setUser({ ...user, district: e.target.value });
                                            getWards(e.target.value);
                                        }}
                                    >
                                        <option value="0">Chn qun/huyn</option>
                                        {districts.map((item: any) => {
                                            if (id && item.district_name.includes(user.district)) {
                                                return (
                                                    <option value={item.district_id} key={item.district_id} selected>
                                                        {item.district_name}
                                                    </option>
                                                );
                                            } else
                                                return (
                                                    <option value={item.district_id} key={item.district_id}>
                                                        {item.district_name}
                                                    </option>
                                                );
                                        })}
                                    </select>
                                    <div className="error_message" style={{ display: 'none' }}></div>
                                </div>

                                <div className="form-group">
                                    <label htmlFor="ward">Phng/x:</label>
                                    <select
                                        name="ward"
                                        id="ward"
                                        className="form-control"
                                        onChange={(e) => setUser({ ...user, ward: e.target.value })}
                                    >
                                        <option value="0">Chn phng/x</option>
                                        {wards.map((item: any) => {
                                            if (id && item.ward_name.includes(user.ward)) {
                                                return (
                                                    <option value={item.ward_id} key={item.ward_id} selected>
                                                        {item.ward_name}
                                                    </option>
                                                );
                                            } else
                                                return (
                                                    <option value={item.ward_id} key={item.ward_id}>
                                                        {item.ward_name}
                                                    </option>
                                                );
                                        })}
                                    </select>
                                    <div className="error_message" style={{ display: 'none' }}></div>
                                </div>
                                <div className="form-group">
                                    <label htmlFor="phonenumber">S in thoi:</label>
                                    <input
                                        name="phonenumber"
                                        id="phonenumber"
                                        type="text"
                                        className="form-control"
                                        onChange={(e) => setUser({ ...user, phone_number: e.target.value })}
                                        value={user.phone_number}
                                    ></input>
                                    <div className="error_message" style={{ display: 'none' }}></div>
                                </div>
                            </div>
                            <div className="col-lg-6">
                                <div className="row">
                                    <div className="form-group">
                                        <label htmlFor="birthday">Ngy sinh:</label>
                                        <input
                                            type="date"
                                            name="birthday"
                                            id="birthday"
                                            className="form-control"
                                            onChange={(e) =>
                                                setUser({
                                                    ...user,
                                                    birthday: e.target.value,
                                                })
                                            }
                                            value={user.birthday.slice(0, 10)}
                                        ></input>
                                        <div className="error_message" style={{ display: 'none' }}></div>
                                    </div>
                                    <div className="form-group" style={{ position: 'relative' }}>
                                        <label htmlFor="password">Mt khu:</label>
                                        <input
                                            type={showPassword === true ? 'text' : 'password'}
                                            name="password"
                                            id="password"
                                            className="form-control"
                                            onChange={(e) =>
                                                setUser({
                                                    ...user,
                                                    password: e.target.value,
                                                })
                                            }
                                            value={user.password}
                                        ></input>
                                        <i
                                            style={{
                                                position: 'absolute',
                                                top: '50%',
                                                right: '5%',
                                                transform: 'translateY(50%)',
                                                cursor: 'pointer',
                                            }}
                                            onClick={() => {
                                                setShowPassword(!showPassword);
                                            }}
                                            className={
                                                showPassword === true ? 'fa-regular fa-eye-slash' : 'fa-regular fa-eye'
                                            }
                                        ></i>
                                        <div className="error_message" style={{ display: 'none' }}></div>
                                    </div>

                                    <div className="form-group">
                                        <label htmlFor="password">Email:</label>
                                        <input
                                            type="text"
                                            name="email"
                                            id="email"
                                            className="form-control"
                                            onChange={(e) =>
                                                setUser({
                                                    ...user,
                                                    email: e.target.value,
                                                })
                                            }
                                            value={user.email}
                                        ></input>
                                        <div className="error_message" style={{ display: 'none' }}></div>
                                    </div>
                                    {id !== undefined && (
                                        <div
                                            className="form-group"
                                            style={{ display: `${id !== undefined ? 'block' : 'none'}` }}
                                        >
                                            <label htmlFor="create_at">Ngy to:</label>
                                            <input
                                                type="date"
                                                name="create_at"
                                                id="create_at"
                                                className="form-control"
                                                onChange={(e) => {
                                                    setUser({
                                                        ...user,
                                                        created_at: e.target.value,
                                                    });
                                                }}
                                                value={user.created_at.slice(0, 10)}
                                            ></input>
                                            <div className="error_message" style={{ display: 'none' }}></div>
                                        </div>
                                    )}
                                </div>
                            </div>

                            <div className="form-group" style={{ display: 'flex', justifyContent: 'space-between' }}>
                                <button
                                    type="button"
                                    style={{ width: '20%', padding: '10px 0px' }}
                                    name="cmd"
                                    className="btn btn-primary btn-add"
                                    onClick={() => (id === undefined ? handleCreateProduct() : handleUpdate())}
                                >
                                    {id !== undefined ? 'Lu Li' : 'Thm mi'}
                                    <i className="fa-solid fa-plus" style={{ marginLeft: '10px' }}></i>
                                </button>
                                <Link
                                    to={'/admin/user'}
                                    className="btn btn-secondary btn-return"
                                    style={{ width: '20%', padding: '10px 0px' }}
                                >
                                    Quay li
                                    <i className="fa-solid fa-arrow-left-long" style={{ marginLeft: '10px' }}></i>
                                </Link>
                            </div>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    );
};
