import React, { useEffect, useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import '../../../../assets/css/Admin/sidebar.css';
import { useRecoilValue, useSetRecoilState } from 'recoil';
import { userState, userValue } from '../../../../store/user.atom';
import { getRoleById } from '../../../../services/role.services';
export const Sidebar = () => {
    const userInfo = useRecoilValue(userValue);
    const setUser = useSetRecoilState(userState);
    const navigate = useNavigate();
    const [role, setRole] = useState({ id: 0, role_name: '' });
    const handleLogOut = async () => {
        setUser({
            id: 0,
            password: '',
            role: 0,
            active: false,
            us_name: '',
            email: '',
            phone_number: '',
            birthday: '',
            created_at: '',
            updated_at: '',
            province: '',
            district: '',
            ward: '',
            token: '',
        });
        navigate('/');
    };
    const hideConfirmationModal = () => {
        setActiveModalConfirm(false);
    };
    useEffect(() => {
        const getRole = async (id: any) => {
            try {
                const res = await getRoleById(id);
                setRole(res);
            } catch (err) {
                console.log(err);
            }
        };
        getRole(userInfo.user.role);
    }, []);
    const [activeModalConfirm, setActiveModalConfirm] = useState(false);
    return (
        <>
            <div className="sidebar active">
                <div className="top">
                    <Link to="/" className="logo" style={{ cursor: 'pointer' }}>
                        <i className="fa-brands fa-codepen" />
                        <span>Ananas</span>
                    </Link>
                    <i className="fa-solid fa-bars" id="btn" />
                </div>
                <div className="user" style={{ marginLeft: ' 11.235px' }}>
                    <div>
                        <p className="bole">{userInfo.user.us_name}</p>
                        <p style={{ textTransform: 'capitalize' }}>{role.role_name}</p>
                    </div>
                </div>
                <ul>
                    {userInfo.user.role === 1 && (
                        <>
                            <li className="">
                                <Link to="/admin/dashboard">
                                    <i className="fa-solid fa-chart-line"></i>
                                    <span className="nav-item">Dashboard</span>
                                </Link>
                                <span className="tooltip">Dashboard</span>
                            </li>
                            <li className="">
                                <Link to="/admin/category">
                                    <i className="fa-solid fa-list"></i>
                                    <span className="nav-item"> Loi sn phm</span>
                                </Link>
                                <span className="tooltip">Loi sn phm</span>
                            </li>
                            <li className="">
                                <Link to="/admin/product">
                                    <i className="fa-solid fa-shoe-prints"></i>
                                    <span className="nav-item">Sn phm</span>
                                </Link>
                                <span className="tooltip">Sn phm</span>
                            </li>
                            <li className="">
                                <Link to="/admin/user">
                                    <i className="fa-regular fa-user"></i>
                                    <span className="nav-item"> Khch hng</span>
                                </Link>
                                <span className="tooltip">Khch hng</span>
                            </li>

                            <li className="">
                                <Link to="/admin/order">
                                    <i className="fa-solid fa-cart-shopping"></i>
                                    <span className="nav-item"> n hng</span>
                                </Link>
                                <span className="tooltip">n hng</span>
                            </li>

                            <li className="">
                                <Link to="/admin/statistics/revenue">
                                    <i className="fa-solid fa-chart-simple"></i>
                                    <span className="nav-item">Thng k</span>
                                </Link>
                                <span className="tooltip">Thng k</span>
                            </li>
                        </>
                    )}

                    {userInfo.user.role === 3 && (
                        <>
                            <li className="">
                                <Link to="/admin/dashboard">
                                    <i className="fa-solid fa-list"></i>
                                    <span className="nav-item">Dashboard</span>
                                </Link>
                                <span className="tooltip">Dashboard</span>
                            </li>
                            <li className="">
                                <Link to="/admin/user">
                                    <i className="fa-solid fa-list"></i>
                                    <span className="nav-item"> Nhn vin</span>
                                </Link>
                                <span className="tooltip">Nhn vin</span>
                            </li>
                        </>
                    )}

                    <li>
                        <Link to="/admin/account">
                            <i className="fa-regular fa-circle-user" />
                            <span className="nav-item"> Ti khon</span>
                        </Link>
                        <span className="tooltip">Ti khon</span>
                    </li>
                    <li>
                        <a href="#" className="btnLogoff" onClick={() => setActiveModalConfirm(true)}>
                            <span style={{ transform: 'rotate(-180deg)' }}>
                                <i className="fa-solid fa-arrow-right-from-bracket" />
                            </span>
                            <span className="nav-item"> ng xut</span>
                        </a>
                        <span className="tooltip">ng xut</span>
                    </li>
                </ul>
            </div>
            <div
                id="modal-confirm-delete"
                className={activeModalConfirm === true ? 'opened' : ''}
                onClick={hideConfirmationModal}
            >
                <div className="modal-confirm-content" onClick={(e) => e.stopPropagation()}>
                    <div className="modal-confirm-header">
                        <h3>Thng bo</h3>
                        <div className="modal-confirm-close">
                            <i
                                className="fa-solid fa-xmark"
                                style={{ color: '#000000' }}
                                onClick={hideConfirmationModal}
                            />
                        </div>
                        <h4 className="modal-confirm-title">Bn chn chn mun ng xut ?</h4>
                    </div>
                    <div className="modal-confirm-action">
                        <button type="button" className="btn btnNo" onClick={hideConfirmationModal}>
                            Hy
                        </button>
                        <button
                            type="button"
                            className="btn btnYes"
                            onClick={() => {
                                handleLogOut();
                            }}
                        >
                            ng 
                        </button>
                    </div>
                </div>
            </div>
        </>
    );
};
