import React, { useEffect, useState } from 'react';
import { OrderType } from '../../../types';
import { CancelOrder, GetListOrderWaitConfirmation, searchOrder } from '../../../services/order.services';
import DataTable, { TableColumn } from 'react-data-table-component';
import { Link } from 'react-router-dom';
import ReactPaginate from 'react-paginate';
import { GetAll } from '../../../services/statusorder.services';
import { toast } from 'react-toastify';

export const Order = () => {
    const [valueSearch, setValueSearch] = useState('');

    const [page, setPage] = useState(1);
    const [pageSize, setPageSize] = useState(6);
    const [pageCount, setPageCount] = useState(0);
    const [statusId, setStatusId] = useState(3);
    const [liststatus, setListStatus] = useState([{ id: 0, status_name: '' }]);
    const [orders, setOrders] = useState<OrderType[]>([
        {
            id: 0,
            user_id: 0,
            receiving_address: '',
            phone_number: '',
            money_total: 0,
            order_date: '',
            update_at: '',
            status_id: 0,
            paymentType_id: 0,
            shippingType_id: 0,
            email: '',
            full_name: '',
            orderDetails: [
                {
                    id: 0,
                    product_id: 0,
                    order_id: 0,
                    quantity: 0,
                    price: 0,
                    size_id: 0,
                    color_id: 0,
                    style_id: 0,
                },
            ],
            totalProduct: 0,
        },
    ]);

    useEffect(() => {
        async function getListStatus() {
            try {
                const res = await GetAll();
                setListStatus(res);
            } catch (err) {
                console.log(err);
            }
        }
        getListStatus();
    }, []);
    const loadData = async () => {
        try {
            const res = await GetListOrderWaitConfirmation({
                pageIndex: page,
                pageSize: pageSize,
                status_id: statusId,
            });
            setOrders(res.data);
            setPageCount(Math.ceil(res.totalItems / pageSize));
        } catch (err) {
            console.log(err);
            setPageCount(0);
            setOrders([]);
        }
    };
    const handleSearchOrder = async () => {
        try {
            const res = await searchOrder({
                pageIndex: page,
                pageSize: pageSize,
                value: valueSearch,
                status_id: statusId,
            });
            console.log(res);
            setOrders(res['data']);
            setPageCount(Math.ceil(res.totalItems / pageSize));
        } catch (err) {
            console.log(err);
            setPageCount(0);
            setOrders([]);
        }
    };
    useEffect(() => {
        loadData();
    }, [statusId, page, pageSize]);
    const handlePageClick = (event: any) => {
        setPage(event.selected + 1);
    };
    const changeInputValue = (e: any) => {
        setPageSize(+e.target.value);
    };

    const columns: TableColumn<OrderType>[] = [
        {
            name: 'ID',
            selector: (row): any => row.id,
            sortable: true,
        },
        {
            name: 'H v tn',
            selector: (row): any => row.full_name,
            sortable: true,
        },

        {
            name: 'S in thoi',
            selector: (row): any => row.phone_number,
            sortable: true,
        },
        {
            name: 'a ch',
            selector: (row): any => row.receiving_address,
            sortable: true,
        },

        {
            name: 'S sn phm',
            selector: (row): any => row.totalProduct,
            sortable: true,
        },
        {
            name: 'Tng tin',
            selector: (row): any => row.money_total.toLocaleString(undefined) + ' VND',
            sortable: true,
        },
        {
            name: 'Chc nng',
            cell: (row) => {
                return (
                    <>
                        <Link
                            to={`/admin/order/order-detail/${row.id}`}
                            className="btn btn-success"
                            style={{ marginRight: '10px' }}
                        >
                            Chi tit
                        </Link>
                    </>
                );
            },
        },
    ];
    return (
        <>
            <div
                className="text-start container"
                style={{
                    marginLeft: 0,
                }}
            >
                <div className="form-group" style={{ display: 'flex', alignItems: 'center' }}>
                    <label htmlFor="status_id">Chn loi n hng</label>
                    <select
                        name="status_id"
                        id="status_id"
                        className="form-control"
                        style={{ width: '20%', marginLeft: '10px' }}
                        onChange={(e) => setStatusId(Number(e.target.value))}
                    >
                        {liststatus.map((status) => {
                            return <option value={status.id}>{status.status_name}</option>;
                        })}
                    </select>
                </div>
            </div>
            <div className="card card-data">
                <div className="card-header">
                    <h3 style={{ textTransform: 'capitalize', fontWeight: 'bold' }}>Danh sch n hng</h3>
                </div>
                <div className="card-body">
                    <div className="form-group text-right" style={{ position: 'relative' }}>
                        <input
                            type="text"
                            onChange={(e) => setValueSearch(e.target.value)}
                            onKeyUp={(e) => {
                                if (e.key.toLocaleLowerCase() === 'enter') {
                                    handleSearchOrder();
                                }
                            }}
                            className="form-control"
                            style={{ width: '25%', paddingLeft: '40px' }}
                        />
                        <i
                            onClick={() => {
                                handleSearchOrder();
                            }}
                            className="fa-solid fa-magnifying-glass btn-search"
                            style={{
                                position: 'absolute',
                                top: '50%',
                                left: '15px',
                                transform: 'translateY(-50%)',
                                cursor: 'pointer',
                            }}
                        ></i>
                    </div>
                    <DataTable columns={columns} data={orders} selectableRows selectableRowsHighlight fixedHeader />
                    <section className="page" style={{ display: `${pageCount > 1 ? 'flex' : 'none'}` }}>
                        <select
                            name="pageSize"
                            className="form-control"
                            onChange={(e) => changeInputValue(e)}
                            value={pageSize}
                        >
                            <option value="5">6</option>
                            <option value="10">12</option>
                            <option value="15">18</option>
                            <option value="20">24</option>
                        </select>
                        <ReactPaginate
                            breakLabel="..."
                            nextLabel={<i className="fa-solid fa-chevron-right"></i>}
                            onPageChange={handlePageClick}
                            pageRangeDisplayed={5}
                            pageCount={pageCount}
                            previousLabel={<i className="fa-solid fa-angle-left"></i>}
                            renderOnZeroPageCount={null}
                        />
                    </section>
                </div>
            </div>
        </>
    );
};
