import { isNumber, showError, showSuccess } from './global';

export const checkEmailError = (input: any) => {
    var regex = /^\w+@[a-zA-Z_]+?\.[a-zA-Z]{2,3}$/;

    if (regex.test(input.value.trim())) {
        showSuccess(input);
        return false;
    } else {
        showError(input, 'Email khng hp l !');
        return true;
    }
};

export const checkPhoneError = (input: any) => {
    const phone = input.value.trim();
    let isError = false;
    for (var i = 0; i < phone.length; i++) {
        let number = parseInt(phone[i]);
        if (!isNumber(number)) {
            showError(input, 'S in thoi khng cha k t ch!');
            isError = true;
        }
    }
    return isError;
};
export const checkBirthDayError = (input: any) => {
    const birthday = input.value.trim();
    const date = new Date(birthday);
    const currentDate = new Date();
    const age = currentDate.getFullYear() - date.getFullYear();
    if (age > 17 && age < 80) {
        return false;
    } else {
        showError(input, 'Ngy sinh khng hp l: tui ca khch hng phi t 18 - 79 !');

        return true;
    }
};
export const checkPasswordError = (input: any) => {
    const pattern = /^((?=\S*?[A-Z])(?=\S*?[a-z])(?=\S*?[0-9]).{6,})\S$/;
    if (pattern.test(input.value.trim())) {
        return false;
    } else {
        showError(input, 'Mt khu phi t 8 k t tr nn, c cha: s, ch hoa thng !');
        return true;
    }
};
export const checkNameError = (input: any) => {
    const name = input.value.trim();
    let isError = false;

    for (var i = 0; i < name.length; i++) {
        let number = parseInt(name[i]);
        if (isNumber(number)) {
            showError(input, 'Tn khch hng khng cha k t s!');
            isError = true;
        }
    }
    return isError;
};
