import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import { BarRevenue } from './BarRevenue';

export const Revenue = () => {
    const [quarter, setQuarter] = useState('');
    return (
        <>
            <div className="text-start container" style={{ marginLeft: '0' }}>
                <select
                    className="form-control"
                    style={{ width: '200px' }}
                    onChange={(e) => {
                        setQuarter(e.target.value);
                    }}
                >
                    <option value="0">Chn qu</option>
                    <option value="qu 1">Qu 1</option>
                    <option value="qu 2">Qu 2</option>
                    <option value="qu 3">Qu 3</option>
                    <option value="qu 4">Qu 4</option>
                </select>
            </div>
            <div className="card">
                <div className="card-header">
                    <h3 style={{ textTransform: 'capitalize', fontWeight: 'bold' }}>
                        Doanh thu qu {quarter != '0' ? quarter : ''}
                    </h3>
                </div>

                <div className="card-body">
                    <BarRevenue quarter={quarter} />
                </div>
            </div>
        </>
    );
};
