import { useEffect, useState } from 'react';
import '../../../assets/css/Shop/shippinginformation.css';
import { useRecoilValue, useSetRecoilState } from 'recoil';
import { cartState, infoValue } from '../../../store/cart.atom';
import { CartItem } from './CartItem';
import { CreateOrder } from '../../../services/order.services';
import { getListPaymentType } from '../../../services/paymentType.services';
import { getListShippingType } from '../../../services/shippingType.services';
import { ShippingTypeItem } from './ShippingTypeItem';
import { PaymentTypeItem } from './PaymentTypeItem';
import { useNavigate } from 'react-router-dom';
import { PaymentType, ShippingType } from '../../../types';
import { OrderDetailsType, CartItemType } from '../../../types';
import axios from 'axios';
import {
    tokenGHN,
    urlApiGetAllProvince,
    urlApiGetDistrictByProvinceId,
    urlApiGetWardByProvinceId,
} from '../../../constant/api';
import { toast } from 'react-toastify';
import { checkEmptyError, handleFocusInput } from '../../../utils/global';
export const ShippingInformation = () => {
    let inputEmail: HTMLElement;
    let inputPhone: HTMLElement;
    let inputFullName: HTMLElement;
    let listInput: any;
    const navigate = useNavigate();
    const cartInfo = useRecoilValue(infoValue);
    const setCarts = useSetRecoilState(cartState);
    const [provinces, setProvinces] = useState([{ ProvinceID: 0, ProvinceName: '' }]);
    const [districts, setDistricts] = useState([{ DistrictID: 0, DistrictName: '' }]);
    const [wards, setWards] = useState([{ WardCode: '', WardName: '' }]);
    const [paymentTypes, setPaymentTypes] = useState<PaymentType[]>([]);
    const [shippingTypes, setShippingTypes] = useState<ShippingType[]>([]);
    const [province, setProvince] = useState('');
    const [district, setDistrict] = useState('');
    const [ward, setWard] = useState('');
    const [village, setVillage] = useState('');
    const [full_name, setFullName] = useState('');
    const [phoneNumber, setPhoneNumber] = useState('');
    const [email, setEmail] = useState('');
    const [paymentTypeId, setPaymentTypeId] = useState(0);
    const [shippingTypeId, setShippingTypeId] = useState(0);
    const [shippingTypePrice, setShippingTypePrice] = useState(0);
    useEffect(() => {
        const queryElement = () => {
            listInput = document.querySelectorAll('.vital');
            inputEmail = document.querySelector('#email') as HTMLElement;
            inputFullName = document.querySelector('#full_name') as HTMLElement;
            inputPhone = document.querySelector('#phone_number') as HTMLElement;
        };
        queryElement();
    });
    useEffect(() => {
        const getPaymentTypes = async () => {
            try {
                const listPaymentTypes = await getListPaymentType();
                setPaymentTypes(listPaymentTypes);
            } catch (error) {
                console.log(error);
            }
        };
        const getShippingTypes = async () => {
            try {
                const listShippingTypes = await getListShippingType();
                setShippingTypes(listShippingTypes);
            } catch (error) {
                console.log(error);
            }
        };
        const getAllProvince = async () => {
            try {
                const res = await axios.get(urlApiGetAllProvince, tokenGHN);
                setProvinces(res.data.data);
            } catch (err) {
                console.log(err);
            }
        };

        const setEventFocusInput = () => {
            handleFocusInput(listInput);
        };
        getPaymentTypes();
        getShippingTypes();
        getAllProvince();
        setEventFocusInput();
    }, []);

    const getListDistricts = async (provinceId: number) => {
        try {
            const res = await axios.post(urlApiGetDistrictByProvinceId, { province_id: provinceId }, tokenGHN);
            setDistricts(res.data.data);
        } catch (err) {
            console.log(err);
        }
    };
    const getWards = async (districtId: number) => {
        try {
            const res = await axios.post(urlApiGetWardByProvinceId, { district_id: districtId }, tokenGHN);
            setWards(res.data.data);
        } catch (err) {
            console.log(err);
        }
    };
    const handleCreateOrder = () => {
        let isError = false;
        const isEmpty = checkEmptyError(listInput);
        if (!isEmpty) {
            if (shippingTypeId === 0) {
                toast.error('Vui lng chn phng thc giao hng !', {
                    position: 'top-right',
                    autoClose: 3000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: 'light',
                });
                isError = true;
            }
            if (paymentTypeId === 0) {
                toast.error('Vui lng chn phng thc giao hng !', {
                    position: 'top-right',
                    autoClose: 3000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: 'light',
                });
                isError = true;
            }
            if (!isError) {
                console.log('oge');
                const list_orderDetail: OrderDetailsType[] = [];

                cartInfo.carts.forEach((cart: CartItemType) => {
                    let orderDetail: OrderDetailsType = {
                        id: 0,
                        order_id: 0,
                        product_id: cart.id,
                        quantity: cart.qty,
                        price: cart.price,
                        color_id: cart.colorId,
                        size_id: cart.size,
                        style_id: cart.styleId,
                    };

                    list_orderDetail.push(orderDetail);
                });
                const data = {
                    full_name: full_name,
                    receiving_address: `${village === '' ? '' : village + '-'}${ward}-${district}-${province}`,
                    phone_number: phoneNumber,
                    email: email,
                    status_id: 3,
                    shippingType_id: shippingTypeId,
                    paymentType_id: paymentTypeId,
                    orderDetails: list_orderDetail,
                    money_total: cartInfo.totalPrice + shippingTypePrice,
                };
                createOrder(data);
            }
        }
    };
    const getShippingType = (shippingId: number): ShippingType => {
        const shippingType: any = shippingTypes.find((item: ShippingType) => item.id === shippingId);
        return shippingType;
    };
    const createOrder = async (order: any) => {
        try {
            const res = await CreateOrder(order);
            localStorage.setItem('cart', JSON.stringify(undefined));
            localStorage.setItem('orderId', JSON.stringify(res));
            navigate('/thankyou');
            setCarts([]);
        } catch (err) {
            console.log(err);
        }
    };
    return (
        <main>
            <div className="main-cart container">
                <div className="row">
                    <div className="col-xs-12 col-sm-12 col-md-7 col-lg-7 main-cart-left">
                        <div className="row">
                            <form action="" id="orderForm">
                                <div className=" col-xs-12 col-sm-12 col-md-12 col-lg-12 title-1">
                                    Thng tin giao hng
                                </div>
                                <div className="col-xs-12 col-sm-12 col-md-10 col-lg-10 form-group">
                                    <input
                                        type="text"
                                        placeholder="H TN"
                                        id="full_name"
                                        className="form-control vital"
                                        onChange={(e) => setFullName(e.target.value)}
                                    />
                                    <div className="error_message" style={{ display: 'none' }}></div>
                                </div>
                                <div className="col-xs-12 col-sm-12 col-md-10 col-lg-10 form-group">
                                    <input
                                        type="text"
                                        placeholder="S in thoi"
                                        id="phone_number"
                                        className="form-control vital"
                                        onChange={(e) => setPhoneNumber(e.target.value)}
                                    />
                                    <div className="error_message" style={{ display: 'none' }}></div>
                                </div>
                                <div className="col-xs-12 col-sm-12 col-md-10 col-lg-10 form-group">
                                    <input
                                        type="text"
                                        placeholder="Email"
                                        id="email"
                                        className="form-control vital"
                                        onChange={(e) => setEmail(e.target.value)}
                                    />
                                    <div className="error_message" style={{ display: 'none' }}></div>
                                </div>
                                <div className="col-xs-12 col-sm-12 col-md-10 col-lg-10 form-group">
                                    <input
                                        type="text"
                                        placeholder="a ch c th s nh, thn, i ..."
                                        id="address"
                                        className="form-control"
                                        onChange={(e) => setVillage(e.target.value)}
                                    />
                                </div>

                                <div
                                    className="col-xs-12 col-sm-12 col-md-10 col-lg-10 form-group location"
                                    id="list-city"
                                >
                                    <select
                                        name="city"
                                        id="city"
                                        className="form-control"
                                        onChange={(e) => {
                                            const province = provinces.find(
                                                (pro) => pro.ProvinceID === Number(e.target.value),
                                            );
                                            setProvince(province?.ProvinceName ?? '');
                                            getListDistricts(Number(e.target.value));
                                        }}
                                    >
                                        <option>Chn tnh / thnh ph</option>
                                        {provinces.map((province: any) => (
                                            <option value={province.ProvinceID} key={province.ProvinceID}>
                                                {province.ProvinceName}
                                            </option>
                                        ))}
                                    </select>
                                </div>
                                <div
                                    className="col-xs-12 col-sm-12 col-md-10 col-lg-10 form-group"
                                    style={{ display: 'flex', justifyContent: 'space-between' }}
                                >
                                    <div
                                        className="col-xs-12 col-sm-12 col-md-5 col-lg-5 form-group location"
                                        id="list-district"
                                    >
                                        <select
                                            name="district"
                                            id="district"
                                            className="form-control"
                                            onChange={(e) => {
                                                const district = districts.find(
                                                    (d) => d.DistrictID === Number(e.target.value),
                                                );
                                                setDistrict(district?.DistrictName ?? '');
                                                getWards(Number(e.target.value));
                                            }}
                                        >
                                            <option value="">Chn qun / huyn</option>
                                            {districts.map((district) => (
                                                <option value={district.DistrictID} key={district.DistrictID}>
                                                    {district.DistrictName}
                                                </option>
                                            ))}
                                        </select>
                                    </div>
                                    <div
                                        className="col-xs-12 col-sm-12 col-md-5 col-lg-5 form-group location"
                                        id="list-commune"
                                    >
                                        <select
                                            name="commune"
                                            id="commune"
                                            className="form-control"
                                            onChange={(e) => {
                                                const ward = wards.find((w) => w.WardCode === e.target.value);
                                                setWard(ward?.WardName ?? '');
                                            }}
                                        >
                                            <option> Chn phng / x </option>
                                            {wards.map((ward) => (
                                                <option value={ward.WardCode} key={ward.WardCode}>
                                                    {ward.WardName}
                                                </option>
                                            ))}
                                        </select>
                                    </div>
                                </div>
                                <div className=" col-xs-12 col-sm-12 col-md-12 col-lg-12 title-1">
                                    Phng thc giao hng
                                </div>
                                <PaymentTypeItem paymentTypes={paymentTypes} setPaymentTypeId={setPaymentTypeId} />

                                <div className=" col-xs-12 col-sm-12 col-md-12 col-lg-12 title-1">
                                    Phng thc thanh ton
                                </div>
                                <ShippingTypeItem
                                    shippingTypes={shippingTypes}
                                    setShippingTypeId={setShippingTypeId}
                                    getShippingType={getShippingType}
                                    setShippingTypePrice={setShippingTypePrice}
                                />
                            </form>
                        </div>
                    </div>
                    <div className="col-xs-12 col-sm-12 col-md-5 col-lg-5 main-cart-right">
                        <ul className="list-group">
                            <li className="list-group-item title">n hng</li>
                            <li className="list-group-item divider" />
                            <CartItem cartlist={cartInfo.carts} totalPriceItem={cartInfo.totalPriceItem} />
                            <li className="list-group-item divider-1"></li>
                            <li className="list-group-item text-1">
                                <span className="title-3">n hng</span>
                                <span className="title-3-1 ">{cartInfo.totalPrice.toLocaleString(undefined)} VND</span>
                            </li>
                            <li className="list-group-item text-2-2">
                                <span className="title-3 lb-discount">Gim</span>
                                <span className="title-4-1">- 0 VND</span>
                            </li>
                            <li className="list-group-item text-2-3 shipping-fee-group">
                                <span className="title-21 label"> Ph vn chuyn</span>
                                <span className="title-22">
                                    <span className="shipping-fee">0</span> VND
                                </span>
                            </li>
                            <li className="list-group-item text-2-3 payment-fee-input">
                                <span className="title-21 label">Ph thanh ton</span>
                                <span className="title-22">
                                    <span className="card-fee">{shippingTypePrice.toLocaleString(undefined)} </span>
                                    VND
                                </span>
                            </li>
                            <li className="list-group-item divider-1"> </li>
                            <li className="list-group-item">
                                <span className="title-5 lb-total-price">Tng Cng</span>
                                <span className="title-5-2">
                                    <span className="total-price">
                                        {(cartInfo.totalPrice + shippingTypePrice).toLocaleString(undefined)} VND
                                    </span>
                                </span>
                            </li>
                            <li className="list-group-item">
                                <button className="btn btn-cart to-checkout" onClick={handleCreateOrder}>
                                    Hon tt t hng{' '}
                                </button>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        </main>
    );
};
